"use client"

import React, { useRef, useState } from 'react';
import { Button } from './ui/button';
import { Card } from './ui/card';
import { calculateAverageColor, estimateTemperature } from '@/lib/thermal-utils';

interface Color {
  r: number;
  g: number;
  b: number;
}

export default function ThermalAnalyzer() {
  const [imageSrc, setImageSrc] = useState<string | null>(null);
  const [averageColor, setAverageColor] = useState<Color | null>(null);
  const [temperature, setTemperature] = useState<number | null>(null);
  const [error, setError] = useState<string>("");
  const [isProcessing, setIsProcessing] = useState(false);
  const fileInputRef = useRef<HTMLInputElement>(null);
  const canvasRef = useRef<HTMLCanvasElement>(null);

  const handleCapture = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) {
      setError('Nenhuma imagem selecionada.');
      return;
    }

    // Check file size (max 5MB)
    if (file.size > 5 * 1024 * 1024) {
      setError('A imagem é muito grande. Por favor, selecione uma imagem menor que 5MB.');
      return;
    }

    if (!file.type.startsWith('image/')) {
      setError('Por favor selecione uma imagem válida.');
      return;
    }

    setIsProcessing(true);
    setError("");
    setImageSrc(null);
    setAverageColor(null);
    setTemperature(null);

    // Set a timeout to prevent infinite processing
    const timeoutId = setTimeout(() => {
      if (isProcessing) {
        setIsProcessing(false);
        setError('Tempo limite excedido. Por favor, tente novamente.');
      }
    }, 10000); // 10 second timeout

    const reader = new FileReader();
    
    reader.onload = (event) => {
      if (!event.target?.result) {
        setError('Erro ao ler a imagem.');
        setIsProcessing(false);
        clearTimeout(timeoutId);
        return;
      }

      const img = new Image();
      
      img.onload = () => {
        try {
          // Check image dimensions
          if (img.width > 4096 || img.height > 4096) {
            throw new Error('Imagem muito grande. Por favor, use uma imagem menor.');
          }

          setImageSrc(img.src);
          const color = calculateAverageColor(img, canvasRef.current);
          if (!color) {
            throw new Error('Não foi possível extrair as cores da imagem.');
          }
          setAverageColor(color);
          const temp = estimateTemperature(color);
          setTemperature(temp);
          setError("");
        } catch (err) {
          setError(err instanceof Error ? err.message : 'Erro ao processar a imagem.');
        } finally {
          setIsProcessing(false);
          clearTimeout(timeoutId);
        }
      };

      img.onerror = () => {
        setError('Erro ao carregar a imagem. Verifique se o formato é suportado.');
        setIsProcessing(false);
        clearTimeout(timeoutId);
      };

      img.src = event.target.result as string;
    };

    reader.onerror = () => {
      setError('Erro ao ler o arquivo. Verifique se o arquivo é uma imagem válida.');
      setIsProcessing(false);
      clearTimeout(timeoutId);
    };

    try {
      reader.readAsDataURL(file);
    } catch (err) {
      setError('Erro ao processar o arquivo. Tente novamente.');
      setIsProcessing(false);
      clearTimeout(timeoutId);
    }
  };

  const handleCaptureClick = () => {
    fileInputRef.current?.click();
  };

  return (
    <div className="space-y-6">
      <input
        type="file"
        accept="image/*"
        capture="environment"
        className="hidden"
        ref={fileInputRef}
        onChange={handleCapture}
      />
      
      <Button 
        onClick={handleCaptureClick}
        className="w-full h-12 text-lg font-medium"
        disabled={isProcessing}
      >
        {isProcessing ? "Processando..." : imageSrc ? "Capturar Nova Imagem" : "Capturar Imagem"}
      </Button>

      {error && (
        <div className="bg-red-50 text-red-600 p-3 rounded-lg text-sm text-center">
          {error}
        </div>
      )}

      {imageSrc && (
        <div className="space-y-6">
          <Card className="overflow-hidden">
            <div className="relative aspect-[4/3]">
              <img
                src={imageSrc}
                alt="Imagem capturada"
                className="w-full h-full object-cover"
              />
            </div>
          </Card>

          {averageColor && (
            <Card className="p-4 space-y-4">
              <div className="flex items-center gap-4">
                <div 
                  className="w-16 h-16 rounded-lg shadow-inner"
                  style={{
                    backgroundColor: `rgb(${averageColor.r}, ${averageColor.g}, ${averageColor.b})`
                  }}
                />
                <div>
                  <div className="text-sm font-medium text-gray-600">Cor Média</div>
                  <div className="font-mono">
                    RGB({averageColor.r}, {averageColor.g}, {averageColor.b})
                  </div>
                </div>
              </div>
              
              {temperature !== null && (
                <div className="border-t pt-4">
                  <div className="text-sm font-medium text-gray-600 mb-1">
                    Temperatura Estimada
                  </div>
                  <div className="text-3xl font-bold tracking-tight">
                    {temperature.toFixed(0)}°C
                  </div>
                  <div className="text-sm text-gray-500 mt-1">
                    Baseado na Lei de Wien
                  </div>
                </div>
              )}
            </Card>
          )}
        </div>
      )}

      <canvas ref={canvasRef} className="hidden" />
    </div>
  );
}
