interface Color {
  r: number;
  g: number;
  b: number;
}

// Calculate average color from an image using canvas
export function calculateAverageColor(img: HTMLImageElement, canvas: HTMLCanvasElement | null): Color {
  if (!canvas) {
    return { r: 0, g: 0, b: 0 };
  }

  const context = canvas.getContext('2d');
  if (!context) {
    return { r: 0, g: 0, b: 0 };
  }

  // Set canvas size to match image
  canvas.width = img.width;
  canvas.height = img.height;

  // Draw image onto canvas
  context.drawImage(img, 0, 0);

  // Get pixel data
  const imageData = context.getImageData(0, 0, canvas.width, canvas.height);
  const data = imageData.data;

  let r = 0;
  let g = 0;
  let b = 0;
  const pixelCount = data.length / 4;

  // Sum all RGB values
  for (let i = 0; i < data.length; i += 4) {
    r += data[i];
    g += data[i + 1];
    b += data[i + 2];
  }

  // Calculate averages
  return {
    r: Math.round(r / pixelCount),
    g: Math.round(g / pixelCount),
    b: Math.round(b / pixelCount)
  };
}

// Estimate temperature based on color values using Wien's displacement law approximation
export function estimateTemperature(color: Color): number {
  // This is a simplified approximation based on color temperature principles
  // Higher blue values generally indicate higher temperatures
  // Lower red values also indicate higher temperatures
  
  const blueWeight = 1.5;  // Blue has stronger correlation with high temperatures
  const redWeight = 1.0;   // Red has inverse correlation
  const greenWeight = 0.5; // Green has less significance

  // Base temperature (arbitrary starting point)
  const baseTemp = 500;  // °C

  // Calculate temperature offset based on color components
  const blueOffset = (color.b / 255) * 1000 * blueWeight;
  const redOffset = ((255 - color.r) / 255) * 500 * redWeight;
  const greenOffset = ((255 - color.g) / 255) * 250 * greenWeight;

  // Combine base temperature with weighted offsets
  const estimatedTemp = baseTemp + blueOffset + redOffset + greenOffset;

  // Ensure temperature stays within reasonable bounds (500°C - 2000°C)
  return Math.max(500, Math.min(2000, estimatedTemp));
}
